.current-waittime {
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
  padding: 1.25rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  transition: box-shadow 0.2s;
}

.current-waittime:hover {
  box-shadow: 0 4px 12px rgba(0, 102, 204, 0.15);
}

.cwt-title {
  font-size: 1.3rem;
  font-weight: 700;
  color: #0066cc;
  text-align: center;
  margin-bottom: 1rem;
}

.cwt-value {
  font-size: 2.5rem;
  font-weight: 800;
  color: #003366;
  text-align: center;
  margin-top: 0.5rem;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
