.waiting-stats-panel {
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  padding: 1.25rem;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  transition: box-shadow 0.2s;
}

.waiting-stats-panel:hover {
  box-shadow: 0 4px 12px rgba(0, 102, 204, 0.15);
}

.panel-title {
  font-size: 1.3rem;
  font-weight: 700;
  color: #0066cc;
  text-align: center;
  margin-bottom: 1rem;
}

.panel-body {
  display: flex;
  gap: 2rem;
  padding: 0 1.5rem;
}

.panel-column {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.label-waiting,
.label-completed {
  font-weight: 600;
  margin-left: 0.5rem;
  font-size: 1.05rem;
}

.label-waiting {
  color: #e68a00; /* 부드러운 오렌지 */
}

.label-completed {
  color: #2e7d32; /* 부드러운 초록 */
}

.list-item {
  font-size: 1rem;
  color: #333;
  padding-left: 1.25rem;
  position: relative;
}

.list-item::before {
  content: "•";
  position: absolute;
  left: 0;
  color: #0066cc;
  font-size: 1.1rem;
  line-height: 1;
}
